* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  text-align: center;
  font-family: Inter, sans-serif;
  background-color: #f2f2f2; /* #23252C; */
  color: rgb(39, 117, 185);
}

.container {
  display: flex;
  position: relative;
  align-items: center;
   width: 50vw; /*play with settings */
  background-color: #eeeeee;
  margin: 0px auto;
  flex-direction: column;
  box-shadow: 0px 0px 10px #44444444; /*K's IDEA */
  /* padding: 0px 20px; K's IDEA */
}

.photo {
  border-radius: 50px;
  width: 25%;
}

.email {
  color: white;
  background-color: rgb(16, 186, 212);
  padding: 1em;
}

h1 {
  color: rgb(39, 117, 185);
  text-shadow: -1px 1px black;
}

h3 {
  color: rgb(39, 117, 185);
  text-shadow: -1px 1px black;
}

p {
  font-family: arial;
  text-align: justify;
  margin: auto;
}


a {
  color: white;
  background-color: rgb(16, 186, 212);
}

.description-section {
  padding: 1em;
}

.footer-links {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 40px;
  margin: auto;
  width: 50vw;
  background-color: rgb(16, 186, 212);
  box-shadow: 0px 0px 10px #44444444
}
